import React, { Component } from 'react';
import Header from './components/header/Header';
import Home from './components/home/Home';
import Menu from './components/menu/Menu';
import Work from './components/work/Work';

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      height: window.innerHeight,
      burgerClasses: "hamburglar is-open",
      burgerToggle: true,
      menuClasses: "menu",
      homeClasses: "home",
      page: "Home"
    }
  }

  componentDidMount(){
    window.addEventListener('resize', (e) => {
      this.setState({height: window.innerHeight});
    });
  }

  toggleBurger = () => {
    this.setState((prevState, prevProps) => {
      return {
        burgerToggle : !prevState.burgerToggle,
        burgerClasses : (prevState.burgerClasses === "hamburglar is-closed") ? 
                          "hamburglar is-open" : 
                          "hamburglar is-closed",
        menuClasses: (prevState.menuClasses === "menu") ?
                      "menu menu-show" :
                      "menu",
        // homeClasses: (prevState.homeClasses === "home home-show home-reset-delays")?
        //               "home home-show home-reset-delays home-move" :
        //               "home home-show home-reset-delays"
      }
    });
  }

  setHomeClasses = (e) => {
    setTimeout(() => { 
      this.setState({homeClasses: 'home home-show'}); 
    }, 1000);
    setTimeout(() => {
      this.setState({homeClasses: 'home home-show home-reset-delays'});
    }, 1500);
  }

  removeHomeClasses = (e) => {
    setTimeout(() => {
      this.setState({homeClasses: 'home'});
    }, 1500);
  }

  navigate = (e) => {
    if(!this.state.burgerToggle) {
      this.toggleBurger();
    }
    this.setState({page: e.target.dataset.page});
  }

  render = () => {
    return (
      <div className="App" style={{height: this.state.height}}>
        <Header 
          burgerStuff={this.state.burgerClasses}
          burger={this.toggleBurger}
          nav={this.navigate} />
        {
          this.state.page === "Home" ? 
          <Home
            classes={this.state.homeClasses}
            setHomeClasses={this.setHomeClasses}
            removeHomeClasses={this.removeHomeClasses}
            nav={this.navigate} />
          : 
          <Work />
        }
        <Menu 
          classes={this.state.menuClasses}
          nav={this.navigate} />
      </div>
    );
  }
}



