import React, { Component } from 'react';

export default class Home extends Component {
	constructor(props) {
		super(props);
		this.state = {
			height: window.innerHeight,
			moveX: null,
			transDur: '0s'
		}
	}

	componentDidMount() {
    window.addEventListener('resize', (e) => {
      this.setState({height: window.innerHeight});
    });
		window.onLoad = this.props.setHomeClasses();
		if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) && window.DeviceOrientationEvent){
			window.addEventListener('deviceorientation', (e)  => {
				this.setState({ moveX: ((e.gamma + 90) * 100) / 180 });
			});
		}
	}

	componentWillUnmount() {
		this.props.removeHomeClasses();
	}

	touchMove = (e) => {
		if(!window.DeviceOrientationEvent) {
			const x = e.target.touches[0].pageX;
			const w = window.innerWidth;
			this.setState({ moveX: (x * 100) / w, transDur: '0s'});
		}
	}
	
	touchStart = (e) => {
		if(!window.DeviceOrientationEvent) {
			const x = e.target.touches[0].pageX;
			const w = window.innerWidth;
			this.setState({ moveX: (x * 100) / w, transDur: '0.5s'});
		}
	}

	mouseMove = (e) => {
		if(!/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
			const x = e.clientX;
			const w = window.innerWidth;
			this.setState({ moveX: `${(x*100) / w}`, transDur: '0s' });
		}
	}

	mouseEnter = (e) => {
		if(!/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)){
			const x = e.clientX;
			const w = window.innerWidth;
			this.setState({ moveX: `${(x*100) / w}`, transDur: '0.5s' });
		}
	}

	render = () => {
		let inc = 7, inc2 = 0, inc3 = 0;
		return (
			<section 
				onMouseMove={this.mouseMove}
				onMouseEnter={this.mouseEnter}
				onTouchMove={this.touchMove}
				onTouchStart={this.touchStart}
				className={this.props.classes}
				style={{
					backgroundPosition: `${this.state.moveX}% 100%`,
					transition: `background-position ${this.state.transDur} 0s, transform 0.5s 0s`
				}}>
				<div id="ripple"></div>
				<h2>
					{
						'Alex'.split('').map((letter, i) => {
							inc3 += i;
							return (
								<div 
									key={i}
									style={{
										transition: `transform 0.5s ${2 + (inc3/100)}s, 
																 opacity 0.5s ${2 + (inc3/100)}s, 
																 color 0.3s 0s, text-shadow 0.3s 0s`
									}}>{letter}</div>
							);
						})
					}
				</h2>
				<h2>
					{
						'Figliolia'.split('').map((letter, i) => {
							inc3 += i;
							return (
								<div 
									key={i}
									style={{
										transition: `transform 0.5s ${2 + (inc3/100)}s, 
																 opacity 0.5s ${2 + (inc3/100)}s, 
																 color 0.3s 0s, text-shadow 0.3s 0s`
									}}>{letter}</div>
							);
						})
					}
				</h2>
				<div>
					<div>
						<h2>
							{
								'Front End'.split('').map((letter, i) => {
									if(letter === ' ') {
										return <div 
											key={i}
											className="space"
											style={{
												transitionDelay: inc/140 + 's'
											}}>&nbsp;&nbsp;</div>
									} else {
										inc-=i;
										return <div 
											key={i}
											style={{
												transitionDelay: inc/140 + 's'
											}}>{letter}</div>
									}
								})
							}
						</h2>
						<h2>
							{
								'Developer'.split('').map((letter, i) => {
									inc2 += i;
									return <div 
										key={i}
										style={{
											transitionDelay: inc2/140 + 's'
										}}>{letter}</div>
								})
							}
						</h2>
					</div>
					<button 
						onClick={this.props.nav} 
						data-page="Work">
						<h3 
							onClick={this.props.nav} 
							data-page="Work">Work</h3>
					</button>
				</div>
			</section>
		);
	}
}



